* {
  margin: 0px;
  padding: 0px;
  text-decoration: none;
}

body {
  background-color: rgb(217, 227, 236);
}

.navBar {
  background: #000;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
}
@media screen and (max-width: 960px) {
  .nav {
    transition: 0.8s all ease;
  }
}

.navBarContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  z-index: 1;
  width: 100%;
  padding: 0 14px;
}

.navLogo {
  color: white;
  font-size: 25px;
  justify-content: flex-start;
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-left: 24px;
  font-weight: bold;
  text-decoration: none;
}

.navLogo spam {
  color: tomato;
}

.login_button {
  width: 100px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin: 20px;
  height: 55px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  border-radius: 50px;
  transition: all 0.4s ease-in-out;

  background-image: linear-gradient(
    to right,
    #0ba360,
    #3cba92,
    #30dd8a,
    #2bb673
  );
  box-shadow: 0 4px 15px 0 rgba(23, 168, 108, 0.75);
}

.login_button:hover {
  background-position: 100% 0;
  transition: all 0.4s ease-in-out;
}

.login_button:focus {
  outline: none;
}

.searchBar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  margin: 50px auto;
  background-size: 300% 100%;
  transition: all 1s ease-in-out;
  background-image: linear-gradient(
    to right,
    #0ba360,
    #3cba92,
    #30dd8a,
    #2bb673
  );
  border-radius: 15px;
  max-width: 750px;
  height: 30px;
}

.searchBar:hover {
  background-position: 100% 0;
  transition: all 1s ease-in-out;
}

.searchInput {
  width: 90%;
  height: 100%;
  border: none;
  color: white;
  font-size: 20px;
  font-weight: 300;
  background-color: transparent;
  outline: 0;
  padding: 0 2px;
  -webkit-appearance: none;
          appearance: none;
  transition: all 0.2 0.1;
  transition-property: width, border-radius;
  z-index: 1;
  position: relative;
}

.submitBtn {
  display: block;
  padding: 10px 15px;
  width: 90px;
  text-align: center;
  font-weight: bold;
  background: white;
  border-radius: 50px;
  border: none;
}
.submitBtn:disabled {
  display: none;
}
.searchInput::-webkit-input-placeholder {
  color: aliceblue;
}
.searchInput::placeholder {
  color: aliceblue;
}

.searchInput:placeholder-shown {
  border-radius: 50px;
  width: calc(100% - 40px);
}

.blogContainer {
  margin: 10px 150px;
  padding: 10px;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-wrap: wrap;
}

@media screen and (max-width: 1580px) {
  .blogContainer {
    margin: 10px 50px;
  }
}

@media screen and (max-width: 1380px) {
  .blogContainer {
    margin: 10px 0px;
  }
}

.blog {
  background-color: white;
  width: 400px;
  height: 580px;
  border-radius: 5px;
  margin: 25px 10px;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.634);
  overflow: auto;
}

.blog a {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.blog a div {
  margin: 2px;
  padding: 10px 20px;
}
.blogImg {
  width: 100%;
  height: auto;
  border-radius: 5px 5px 0 0;
}

.blogSource {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.blogSource span {
  font-size: 14px;
  font-weight: 300;
  color: rgb(39, 39, 39);
  margin: 2px 0;
  padding: 4px 8px;
  border-radius: 5px;
}

.blogSource span:nth-child(1) {
  background-color: rgb(225, 134, 23);
}

h2 {
  color: black;
  padding: 3px 0;
  margin: 5px 0;
}

p {
  padding: 3px 0;
  margin: 5px 0;
  color: gray;
}

.noResultFound {
  background-color: whitesmoke;
  border: 8px soild black;
  margin: auto;
  border-style: outset;
  padding: 20px;
}

